body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form__label {
  display: block;
  width: 75%;
  margin: 0 auto;
  font-size: 1rem;
  font-weight: bold; }
  .form__label input,
  .form__label textarea {
    box-sizing: border-box;
    display: block;
    width: 100%;
    margin: .5rem 0;
    padding: .3rem;
    border: 1px solid #353535;
    border-radius: 5px;
    color: #353535; }
  .form__label input:focus,
  .form__label textarea:focus {
    outline-color: #035354; }

.form__btns {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 75%;
  margin: 1rem auto .5rem auto; }

.form .btn--cancel {
  background-color: #F2234C; }

.form .btn--cancel:hover {
  background-color: #CC1D40; }

.table-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  max-height: 35rem; }
  .table-section__table {
    width: 100%;
    margin: 1rem 0;
    box-sizing: border-box;
    padding: .5rem;
    overflow: auto; }
    .table-section__table table {
      width: 100%; }
  .table-section__thead {
    background-color: #035354;
    color: #f7f7f7;
    font-weight: bold; }
  .table-section__tbody tr {
    transition: all .5s; }
  .table-section__tbody tr:hover {
    background-color: #C2E5DF; }
  .table-section th,
  .table-section td {
    border-bottom: 1px solid #035354;
    padding: 15px 10px; }
  .table-section caption {
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: .5rem;
    text-transform: uppercase; }

.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5); }

.modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 60%;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 10px;
  left: 20%;
  top: 5%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
  border-radius: 10px; }
  .modal h2 {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    margin: .5rem; }


html,body{
    box-sizing: border-box;
    padding: 1rem;
    color: #035354;
    background-color: #CFF5EE;

}

h1{
    margin: 1rem;
}

.btn{
    padding: .5rem 1rem;
    display: inline-block;
    border-radius: 5px;
    box-shadow: -1px 1px 2px #353535;
    text-decoration: none;
    color: #F1FFFF;
    cursor: pointer;
    background-color: #50A5A6;
    transition: .5s;
    font-weight: bold;
}

.btn:hover{
    background-color: #448B8C;
}
